@import "../../../common/styles/sass/variables";
@import "../../../common/styles/sass/mixns";

.postContainer {
  display: flex;
  flex-direction: column;
  row-gap: 15px;
  .banner {
    img {
      width: 300px;
      height: 180px;
      background-color: $secondBGColor;
    }
  }
  .content {
    display: flex;
    column-gap: 15px;
    .img {
      img {
        width: 48px;
        height: 48px;
        background-color: $secondBGColor;
        border-radius: 50%;
      }
    }
    .text {
      display: flex;
      flex-direction: column;
      .navPost{
        text-decoration: none;
        color: $secondFontColor;
        .title {
          margin: 0;
          @include text(600, 16px, 24px)
        }
      }
      .description {
        @include text(400, 14px, 24px);
        color: $fontColor;
      }
      .date {
        @include text(400, 12px, 16px);
        color: $fontColor;
      }
    }
  }
}